<template>
  <div class="catalog-item">
    <img class="catalog-item__img" :src="item.image" />
    <h1 class="catalog-item__name">{{ item.name }}</h1>
    <div class="catalog-item__characteristic">
      <div class="catalog-item__handler-wrapper">
        <div
          v-for="type in item.types"
          :key="type.index"
          class="catalog-item__handler catalog-item__type active"
        >
          {{ type }}
        </div>
      </div>
      <div class="catalog-item__handler-wrapper">
        <div
          v-for="size in item.sizes"
          :key="size.index"
          class="catalog-item__handler catalog-item__size active"
        >
          {{ size }} см.
        </div>
      </div>
    </div>
    <div class="catalog-item__footer">
      <div class="catalog-item__price">от {{ item.price }} ₽</div>
      <button 
        @click.prevent="addItemToBasket"
        class="catalog-item__btn"
      >
        Добавить
      </button>
    </div>
  </div>
</template>

<script>
import store from '../store/index'

export default {
  props: ["item"],
  data () {
    return {
      addItem: this.item
    }
  },
  methods: {
    addItemToBasket() {
      store.state.addedItems.push(this.addItem)
      store.dispatch('totalCost')
    }
  }
};
</script>

<style lang="scss">
.catalog-item {
  display: flex;
  flex-direction: column;
  width: 280px;
  text-align: center;

  &__img {
    margin-bottom: 10px;
  }

  &__name {
    font-weight: 800;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.01em;
    color: #000000;
    margin-bottom: 22px;
  }

  &__characteristic {
    background: #f3f3f3;
    border-radius: 10px;
    padding: 5px;
    margin-bottom: 17px;
  }

  &__handler-wrapper {
    display: flex;
    gap: 8px;

    &:not(:last-child) {
      margin-bottom: 7px;
    }
  }

  &__handler {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    letter-spacing: 0.015em;
    color: #2c2c2c;

    &.active {
      background: #ffffff;
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
      border-radius: 5px;
    }
  }

  &__type,
  &__size {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 32px;
    cursor: pointer;
  }

  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__price {
    font-weight: 700;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
    color: #000000;
  }

  &__btn {
    background: #FE5F1E;
    border: 1px solid #FE5F1E;
    border-radius: 30px;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    padding: 10px 15px;
    cursor: pointer;
  }
}
</style>